<template>
  <div class="pokemons">
    <h1>Carga de pokemons: </h1>
    <div class="row">
        <div class="col s6 offset-s3 m4 offset-m4 l2 offset-l5">
            <select class="browser-default" v-model="p" @change="go">
                <option :value="item" v-for="(item,index) in 700" :key=index>{{item}}</option>
            </select>
        </div>
    </div>
    <div class="row">
        <div class="col s12" style="min-height:300px">
            <Loader v-if="objeto.name == ''"></Loader>
            <Pokemon :id="''+p" :nombre="objeto.name" :url="objeto.sprites.front_default" v-if="objeto.name != ''"></Pokemon>
        </div>
    </div>
    
  </div>
</template>

<script>

import axios from "axios";
import Pokemon from "@/components/Pokemon.vue";
import Loader from "@/components/Loader.vue";

export default {
    components :{
        Pokemon, Loader
    },
    data(){
        return {
            p : 1,
            objeto : {
                name : "",
                sprites : {
                    front_default : ""
                }
            }
        }
    },
    created(){
        this.p = this.$route.params.id;
        this.getPoke(this.$route.params.id);
    },
    methods : {
        go : function(){
            this.objeto =  {
                name : "",
                sprites : {
                    front_default : ""
                }
            };
            this.$router.push("/pokemons/"+this.p);
            this.getPoke(this.p);
        },
        async getPoke(id){
            let salida = await axios.get('https://pokeapi.co/api/v2/pokemon/'+id);
            this.objeto = await salida.data;
        }
    }
}
</script>
